// frontend/src/services/aiService.js
// Service pour communiquer avec l'assistant IA du backend

const BACKEND_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000';

console.log(' AI Service Backend URL:', BACKEND_URL);

// Fallback suggestions intelligentes
const getFallbackResponse = (prompt) => {
  const msg = prompt.toLowerCase();
  
  if (msg.includes('crer') || msg.includes('nouveau')) {
    return 'Pour crer un nouveau CV, utilisez le bouton "Crer un nouveau CV" dans le menu principal. Vous pourrez alors remplir vos informations personnelles et choisir un modle parmi les modles disponibles.';
  }
  if (msg.includes('comptence') || msg.includes('skill')) {
    return 'Les comptences cls  ajouter dpendent de votre domaine:\n Pour l\'IT: JavaScript, Python, React, SQL\n Pour HR: Recrutement, SIRH, Paie\n Pour Marketing: SEO, Social Media, Analytics\n Pour Finance: Excel, SAP, Analyse financire';
  }
  if (msg.includes('exporter') || msg.includes('export') || msg.includes('pdf')) {
    return 'Pour exporter votre CV:\n1. Remplissez le formulaire de cration\n2. Cliquez sur "Exporter le CV"\n3. Choisissez le format (PDF, DOCX, etc.)';
  }
  if (msg.includes('modle') || msg.includes('template')) {
    return 'Nous proposons 4 modles de CV:\n Moderne: Design contemporain pour tous les secteurs\n Classique: Style professionnel traditionnel\n Cratif: Parfait pour les mtiers cratifs\n Minimaliste: pur et sans fioritures';
  }
  if (msg.includes('lettre') || msg.includes('motivation')) {
    return 'Pour crer une lettre de motivation, utilisez l\'onglet "Crer une Lettre" dans le menu. Vous pourrez personnaliser votre lettre selon le poste vis.';
  }
  if (msg.includes('aide') || msg.includes('help') || msg.includes('comment')) {
    return 'Comment puis-je vous aider?\n Crer un CV \n Gnrer une lettre de motivation \n Consulter les modles \n Exporter en PDF \n Trouver des comptences pertinentes ';
  }
  
  return 'Je suis l\'assistant CV. Comment puis-je vous aider? Vous pouvez me poser des questions sur la cration de CV, les comptences  ajouter, les modles disponibles, ou comment exporter votre CV.';
};

export const getAISuggestions = async (prompt) => {
  try {
    if (!prompt || prompt.trim() === '') {
      throw new Error('Le prompt ne peut pas tre vide');
    }

    // Try to connect with a timeout
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 8000); // 8s timeout

    const response = await fetch(`${BACKEND_URL}/api/assistant`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ prompt }),
      signal: controller.signal
    });

    clearTimeout(timeoutId);

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`HTTP ${response.status}: ${errorText}`);
    }

    const data = await response.json();
    
    if (!data.success) {
      throw new Error(data.error || 'Erreur de l\'assistant IA');
    }

    return {
      success: true,
      response: data.response,
      suggestions: [
        'Communication et travail d\'quipe',
        'Rsolution de problmes complexes',
        'Gestion de projet et organisation',
        'Matrise des outils bureautiques',
        'Adaptabilit et flexibilit',
        'Leadership et mentorat'
      ]
    };
  } catch (error) {
    console.error(' Erreur AI Assistant:', error);
    
    // Use intelligent fallback instead of generic error
    const fallbackResponse = getFallbackResponse(prompt);
    
    return {
      success: true,  // Return true with fallback response
      response: fallbackResponse,
      fromFallback: true
    };
  }
};
